<template>
  <div>
    <canvas id="renderCanvas"></canvas>
    <div class="description">
      <div class="item">
        <h2>框架</h2>
        <div class="content">
          <p>
            <i>起因</i>：通过debug网站：
            <a target="_blank" href="https://www.3dreal.com/">https://www.3dreal.com/</a>，发现其所用的3D渲染框架为Babylon.js
          </p>
          <p>
            <i>自述</i>：Babylon.js: Powerful, Beautiful, Simple, Open - Web-Based 3D At Its Best.
          </p>
          <p>
            <i>wiki百科</i>：Babylon.js是一个实时 3D引擎，使用JavaScript库通过HTML5在Web浏览器中显示3D图形。源代码在GitHub上可用，并根据Apache License 2.0 分发。它最初于2013年发布，由两位Microsoft员工David Catuhe和David Rousset在空闲时间开发，并由艺术家Michel Rousseau作为3D游戏引擎进行了开发。在2015年巴黎的WebGL会议上发表。
          </p>
          <p>
            <i>github</i>：目前拥有Star数量11.2k，地址：
            <a
              target="_blank"
              href="https://github.com/BabylonJS/Babylon.js"
            >https://github.com/BabylonJS/Babylon.js</a>
          </p>
        </div>
      </div>
      <hr />
      <div class="item">
        <h2>核心</h2>
        <div class="content">
          <p>
            <i>前端</i>
            ：如果需要使用自定义的3D模型，就需要用到Babylon.js的SceneLoader方法：通过构造函数实例化一个canvas画布对象，使这个对象根据引入的.glTF文件或GLB文件内的节点信息进行3D渲染，并具备一些交互操作。
          </p>
          <img src="../assets/maincode.png" />
          <p>
            <i>素材导入和使用</i>：除了粒子等少数元素，场景和物体（包含物体的动画）都是外部导入素材。目前最流行的素材统一格式是.gltf。素材一般由 .gltf，.bin 和 textures （皮肤） 文件组成。
          </p>
          <p>
            <i>glTF文件</i>：glTF文件是描述三维场景以及坐标、颜色、贴图的文件的集合。
          </p>
          <img src="../assets/mainpic.png" />
          <p>
            <i>GLB文件</i>：GLB 文件利用包含定义场景中三维对象所需的全部信息的二进制文件，降低管理三维内容传输的难度。
          </p>
          <p>备注：blender 或者 unity 均可制作 3D 建模并导出 glTF 文件。GLB 文件可转换为 glTF 文件，反之亦然。</p>
        </div>
      </div>
      <hr />
      <div class="item">
        <h2>看法</h2>
        <div class="content">
          <p>1.如果前后端分离，调用接口进行数据交互，则更倾向于 GLB 文件格式。否则就是前端存储相关模型，对用户的点击和选择通过切换 glTF 文件实现不同的渲染。对于简单的颜色处理，可以使用内置的着色函数。</p>
          <p>2.需要有人来进行3D建模。</p>
        </div>
      </div>
    </div>
